.detalles{
    -webkit-animation:slide-in-fwd-center 1s cubic-bezier(.25,.46,.45,.94) both;animation:slide-in-fwd-center 1s cubic-bezier(.25,.46,.45,.94) both;
    @-webkit-keyframes slide-in-fwd-center{0%{-webkit-transform:translateZ(-1400px);transform:translateZ(-1400px);opacity:0}100%{-webkit-transform:translateZ(0);transform:translateZ(0);opacity:1}}@keyframes slide-in-fwd-center{0%{-webkit-transform:translateZ(-1400px);transform:translateZ(-1400px);opacity:0}100%{-webkit-transform:translateZ(0);transform:translateZ(0);opacity:1}}
            gap: 5px;
            margin: 2rem auto;
            width: 90%;
            min-height: 700px;
            box-shadow: 0px 0px 20px 8px $content-bg-dark;
            border: solid 2px $primary-color ;
            background-color: $primary-color ;
            &:hover{ transform: none;
            }
            .close-circle{
                color:yellow;
                background-color: black;
                border-radius: 100px;
                font-size:2.5em;
                position:absolute;
                top: 1;
                right:5%;
                margin: 8px;
                @include hover-d;              
                &:hover{
                    transform: scale(1.2);
                }
            }

            .prod-img{
                height: 50%;
                    img{
                        border-radius: 18px 18px 0 0;
                        padding: 0px;
                    }
            }
    
            .prod-info{
                width: 100%;
                flex-direction: column;
                height: 15%;   

            }
    
            .nombre-prod, .precio-prod{
                justify-content:center;
                text-align: center;
            }
                .nombre-prod{
                    width: 100%;
                    font-size: 1.9em;
                }
                .precio-prod{
                    margin-right: 0;
                    width: auto;
                    padding: 5px 40px;
                    font-size: 1.5em;
                    box-shadow: none;   
                    color:$text-w;
                    font-weight: 700;
                    background-color:$secundary-color;
                }
                .detalles-prod{
                    height: 15%;
                    width: 100%;
                    padding: .5rem 12px;
                    font-size: 1.2em;
                    color: black;
                    cursor: default;
                    border-radius: 0px;
                    pointer-events: none;
                    margin: .5rem auto;

                }
                .btn-prod-cart{
                    gap: 8px;
                    height: 20%;

                }
                  

            @media ( min-width: 785px ){
                &:hover{
                    .detalles-prod{
                        color: $text-w;
                    }
                }
                min-height: 750px;
                gap: 0px;
                background-color: rgba(0, 0, 0, 0.85);
                color: $text-w;
                .precio-prod{
                    background-color: $primary-color;
                    color: $secundary-color;
                    border: solid .5px  $secundary-color;
                    box-shadow: 0 0 2px 1px  $primary-color;
                }
                .btn-cont{
                    color: white;
                    transition: all .5s;
                    &:hover{
                        color: black;
                        background-color: white;
                    }
                }
                .nombre-prod{
                    color: $text-w;
                }
                .prod-img{
                    height: 65%;
                }
                .detalles-prod{
                    height: 12%;
                    margin: 1rem auto;
                    padding: 0 3.5rem;
                    color: $text-w;

                }                    
                .btn-prod-cart{
                    height: 8%;
                    gap: 0px;
                    width: 60%;
                    flex-direction: row;
                    justify-content: space-between;
                    margin: 0 auto 8px;
                }
    
                    .cont-prod{
                        height: 100%;
                        width: 25%;
                        margin: 0 auto;
                    }
                    .stock{
                        @include flex-center;  
                        height: 100%;
                        width: 10%;
                        text-align: center;
                        justify-content: center;
                        margin: 0 auto
    
                    }
                    .contenedorDeBotones{
                        width: 35%;
                        height: 100%;
                        transition: all ease .5s;       

                        border: solid 1.5px  $secundary-color;
                        box-shadow: 0 0 6px 2px $primary-color;
                        border-radius: 100px;
                        margin: 0 auto;        
                        background-color:$primary-color;
                        .addToFav{
                            width: 25%;
                            color: $secundary-color;
                            border-radius:  90px 0 0 90px;

                            &:hover{
                                color:$primary-color;
                                background-color: black;
                            }
                            svg{
                                width: 50%;
                            }
                        }
                        .agregar-carrito{
                            width: 75%;
                            padding: 10px;
                            border-radius:  0 90px 90px 0 ;
                            color: $secundary-color;
                            &:hover{
                                color:$primary-color;
                                background-color: black;
                            }
                        } 
                    }
           
            }

}    


